function tabsInit(){ 
	$('.tabs__content').not('.tabs__content_active').hide(); 
	$('.tabs-list__item button').click(function(){ 
		$('.tabs-list__item').not($(this).parent()).removeClass('tabs-list__item_active'); 
		$(this).parent().addClass('tabs-list__item_active'); 
		$('.tabs__content').not('#'+$(this).attr('data-content')).removeClass('tabs__content_active').hide(); 
		if ($(window).outerWidth() > 512) {
			$('#'+$(this).attr('data-content')).addClass('tabs__content_active').fadeIn(); 
		}else{
			$('.tabs__content').removeClass('tabs__content_active');
		}
	}); 
}; 

$(function(){
	tabsInit();
	tabsMobile();
})

$(window).resize(function(){
	$(function(){
		tabsInit();
		tabsMobile();
	})
})

function tabsMobile(){
	var $item = $('.tabs-list__item'),
			$itemBtn = $item.find('button'),
			$content = $('.tabs__content');

	if ($(window).outerWidth() <= 512) {
		$itemBtn.each(function(){
			if ( $(this).attr('data-content') == 'content1' ) {
				$(this).after( $('#'+$(this).data('content')) );
			}else if ( $(this).attr('data-content') == 'content2' ) {
				$(this).after( $('#'+$(this).data('content')) );
			}else if ( $(this).attr('data-content') == 'content3' ) {
				$(this).after( $('#'+$(this).data('content')) );
			}
		})
	}else{
		var tabsContent = $('.tabs').find('.tabs__content').detach();
		$('.tabs-list').after(tabsContent);
	}
}
burgerDropdown('.tabs-list__item button','.tabs__content');