function toggleModal(menu,btn){
	var $menu = $(menu),
			$btn = $(btn),
			$body = $('body'),
			$link = $('<a href="#" class="close-link" onclick="return false"></a>'),
			bodyClass = 'white-overlay',
			menuClass = 'show',
			linkClass = 'active';

	var createLink = function(){
		if ($('.close-link').length == 0 && $(window).outerWidth() < 513) {
			$body.prepend($link);
		}else if($('.close-link').length > 1){
			$('.close-link').remove();
		}
	}

	var toggleMenu = function() {
		$menu.toggleClass(menuClass);
		$body.toggleClass(bodyClass);
		$link.toggleClass(linkClass);
	};

	$btn.on('click', function(){
		createLink();
		toggleMenu();
	});

	$(document).on('click', function(event){
		if($(event.target).closest($btn).length || $(event.target).closest($menu).length){
			return;
		}else{
			$menu.hasClass(menuClass) && toggleMenu();
		}
	});
}
toggleModal('.burger-menu', '.toggle-menu');
toggleModal('.additional-menu', '.toggle-menu2');