var isVisited = window.localStorage.getItem('visited');

$('.bot-line__hide').addClass('bot-line__hide-rotate');
$('.bot-line__inner').slideUp();
if (!isVisited) {
	$('.bot-line__hide').removeClass('bot-line__hide-rotate');
	$('.bot-line__inner').slideDown();
};

$('.bot-line__hide').on('click', function() {
	if ($('.bot-line__inner').is(':visible')) {
		$(this).addClass('bot-line__hide-rotate');
		$('.bot-line__inner').slideUp();
		window.localStorage.setItem('visited', 'true');
	}else{
		$(this).removeClass('bot-line__hide-rotate');
		$('.bot-line__inner').slideDown();
		window.localStorage.removeItem('visited');
	}
});

function footCont(){
	var	$text = $('.footer__bottom span');

	$text.each(function(){
		var $link = $(this).find('a').detach();

		if ($(window).outerWidth() <= 512) {
			$(this).append($link);
		}else{
			$(this).prepend($link);
		}
	})
}
footCont();

$(window).resize(function(){
	footCont();
})