function navList(link){
	var $link = $(link);
	
	var menuArrow = function(elem){
		$link.each(function(){
			if ($(window).outerWidth() <= 512){
				if ($(this).next('.nav-hidden').length){
					$(this).append('<svg class="nav-list__arrow" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" preserveAspectRatio="xMidYMid" width="11.875" height="6.531" viewBox="0 0 11.875 6.531">\
														<path d="M11.673,0.988 L6.323,6.338 C6.096,6.565 5.727,6.565 5.500,6.338 L0.150,0.988 C-0.077,0.761 -0.077,0.393 0.150,0.166 C0.378,-0.062 0.746,-0.062 0.973,0.166 L5.912,5.104 L10.850,0.166 C11.077,-0.062 11.445,-0.062 11.673,0.166 C11.900,0.393 11.900,0.761 11.673,0.988 Z" class="cls-1"/>\
													</svg>');
				}
			}else if ($(window).outerWidth() > 512){
				$('.nav-list__arrow').remove();
			}
		})
	}

	menuArrow();
	
	$(window).resize(function(){
		menuArrow();
	})

	$link.on('click', function(e){
		var	$this = $(this),
				$menuHid = $this.next('.nav-hidden');

		if ($(window).outerWidth() <= 512) {
			if ($menuHid.length) {
				e.preventDefault();

				if ($menuHid.is(':hidden')){
					$('.nav-hidden').slideUp();
					$('.nav-list__link').removeClass('active');

					$this.addClass('active');
					$menuHid.slideDown();
				}else{
					$menuHid.slideUp();
					$link.removeClass('active');
				}
			}else{
				$('.nav-hidden').slideUp();
				$('.nav-list__link').removeClass('active');
			}
		}
	})
}

navList('.nav-list__link');