function fixedNav(){
	var $nav = $('.fixed-nav'),
			$navOffsetTop = $nav.offset().top,
			$navList = $nav.find('.fixed-list');

	var fixTop = function(){
		if ($('body').scrollTop() > $navOffsetTop) {
			$nav.addClass('fixed');
		}else if ($('body').scrollTop() < $navOffsetTop){
			$nav.removeClass('fixed');
		}
	}
	fixTop();

	var addLogo = function(){
		if ($nav.hasClass('fixed') && $nav.find('.fixed-nav__logo').length == 0) {
			$navList.before('<a href="#" class="fixed-nav__logo"><img src="assets/img/fix-logo.png" alt="Логотип" /></a>');

		}else if (!$nav.hasClass('fixed')) {
			$('.fixed-nav__logo').remove();
		}
	}
	addLogo();

	var addBotNav = function(){
		if ($nav.hasClass('fixed') && $nav.find('.btn-nav').length == 0) {
			$('.fixed-nav__logo').after('<button class="btn-nav">\
																	 	<span class="btn-nav__line"></span>\
																	 	<span class="btn-nav__line"></span>\
																	 	<span class="btn-nav__line"></span>\
																	 </button>');

		}else if (!$nav.hasClass('fixed')) {
			$('.btn-nav').remove();
		}

		if ($nav.hasClass('fixed') && $nav.find('.fixed-submenu').length == 0) {
			$nav.append('<div class="fixed-submenu">\
									 	<div class="container">\
								 			<ul class="fixed-submenu__list">\
								 				<li class="fixed-submenu__item"><a href="#" class="fixed-submenu__link">Ремонт iPhone / 7</a></li>\
								 				<li class="fixed-submenu__item"><a href="#" class="fixed-submenu__link">Ремонт iPad / Pro</a></li>\
								 				<li class="fixed-submenu__item"><a href="#" class="fixed-submenu__link">Ремонт iPod</a></li>\
								 				<li class="fixed-submenu__item"><a href="#" class="fixed-submenu__link">Ремонт MacBook / Pro</a></li>\
								 				<li class="fixed-submenu__item"><a href="#" class="fixed-submenu__link">Ремонт Apple Watch</a></li>\
								 			</ul>\
									 	</div>\
								  </div>');

		}else if (!$nav.hasClass('fixed')) {
			$('.fixed-submenu').remove();
		}
	}
	addBotNav();

	$nav.on('click', '.btn-nav', function(){
		$(this).toggleClass('btn-nav-open');
		$('.fixed-submenu').toggleClass('fixed-submenu-open');
	})

	$(window).on('scroll', function(){
		fixTop();
		addLogo();
		addBotNav();
	})
}

$(function(){
	fixedNav();
})