function headerTablet(){
	var $addMenu = $('.additional-menu'),
			$headerMid = $('.header-middle-mobile');

	if ($(window).outerWidth() < 992) {
		$('.login').after($('.header-top__city'));
		$('.btn-settings').before($('.header-middle__basket'));
		
		$headerMid.prepend($('.header-middle__logo'));
		$addMenu.append($('.header-middle__number'));
		$addMenu.append($('.header-middle__time').first());
	}else{
		$('.header-middle__content').prepend($('.header-middle__logo'));
		$('.header-middle__logo').after($('.header-middle__time').first());
		$('.header-middle__time').first().after($('.header-middle__number'));
	}

	if ($(window).outerWidth() < 992) {
	}else{
		$('.header-top__wrap-city').append($('.header-top__city'));
		$('.header-middle__time').last().append($('.header-middle__basket'));
	}
}
headerTablet();


function headerMobile(){
	if ($(window).outerWidth() < 513 && $('.header-middle-mobile__time').find('a').length < 1) {
		$('.header-middle-mobile__time').prepend('<a href="tel:+74958888888">+7 (495) 888-88-88</a>');
	}else if($(window).outerWidth() > 512){
		$('.header-middle-mobile__time').find('a').remove();
	}
}
headerMobile();


$(window).resize(function(){
	headerTablet();
	headerMobile();
})