function mobileModals(){
	var $leftMenu = $('.burger-menu'),
			$rightMenu = $('.additional-menu'),
			$body = $('body');

	var move = function(){
		if ($(window).outerWidth() < 513 && $leftMenu.length == 1){
			$body.prepend($leftMenu);
			$leftMenu.after($rightMenu);
		}else{
			$('.btn-settings').after($leftMenu);
			$leftMenu.after($rightMenu);
		}
	}
	move();

	$(window).resize(function(){
		move();
	})
}

$(function(){
	mobileModals();
})