function burgerDropdown(item,list){
	var $item = $(item),
			$list = $(list);

	$item.on('click', function(e){
		var $this = $(this),
				$dropdown = $this.next($list);

		if ($dropdown.length) {
			e.preventDefault();

			if ( $dropdown.is(':hidden') ) {
				$list.slideUp();
				$item.removeClass('rotate');
				
				$dropdown.slideDown();
				$this.addClass('rotate');
			}else{
				$dropdown.slideUp();
				$this.removeClass('rotate');
			}
		}
	})
}
burgerDropdown('.burger-list__title','.burger-dropdown');