function createSlick(){  
	$(".sidebar-products").not('.slick-initialized').slick({
		slidesToShow: 3,
		SlidesToScroll: 1,
		// autoplay: true,
		autoplaySpeed: 2000,
		centerMode: true,
		centerPadding: '0',
		responsive: [
		{
			breakpoint: 767,
			settings: {
				slidesToShow: 1,
			}
		}
		]
	});	
}

if ( $(window).outerWidth() > 991 && $('.sidebar-products').hasClass('slick-slider') ) {
	$('.sidebar-products').slick('unslick');
}else if ( $(window).outerWidth() <= 991 ) {
	createSlick()
}

$(window).on( 'resize', function(){
	if ( $(window).outerWidth() > 991 && $('.sidebar-products').hasClass('slick-slider') ) {
		$('.sidebar-products').slick('unslick');
	}else if ( $(window).outerWidth() <= 991 ) {
		createSlick()
	}
});