$(function(){
	if ($(window).outerWidth() <= 991) {
		blockHeight('.sidebar-product',767);

		$('.sidebar-product').each(function(){
			var elem = $(this).find($('.sidebar-product__tooltip'));
			elem.appendTo($(this).find($('.sidebar-product__bottom')));
		})
	}
})

$(window).resize(function(){
	$(function(){
		if ($(window).outerWidth() <= 991) {
			blockHeight('.sidebar-product',767);

			$('.sidebar-product').each(function(){
				var elem = $(this).find($('.sidebar-product__tooltip'));
				elem.appendTo($(this).find($('.sidebar-product__bottom')));
			})
		}
	})
})