$('.livechat-girl').on('click', function(){
	if (!$('.modal-block').hasClass('rotated')) {
		$('.modal-block').removeClass('rotatedBack');
		$('.modal-block').fadeIn();
		$('.modal-block').addClass('rotated');
	}else{
		$('.modal-block').removeClass('rotated');
		$('.modal-block').fadeOut();
		$('.modal-block').addClass('rotatedBack');
	}
})

$('.livechat-girl').hover(
	function(){
		$(this).find('.livechat-hint').show();
		$(this).find('.livechat-hint').removeClass('hide_hint');
		$(this).find('.livechat-hint').addClass('show_hint');
	},

	function(){
		$(this).find('.livechat-hint').removeClass('show_hint');
		$(this).find('.livechat-hint').addClass('hide_hint');
	})


$(document).ready((function(){
	function f1(){
		$('.livechat-girl').find('.animated-circles').addClass('animated')
	}
	function f2(){
		$('.livechat-girl').find('.animated-circles').removeClass('animated')
	}
	var i=0;
	var funclist = [f1,f2];

	function next(){
		i = ++i % funclist.length;
		return funclist[i]();
	}

	function REC(){
		next();
		setTimeout(REC,3000)
	}
	return REC;
})());