
$(function() {
	var nice = $(".news-wrap-overflow--2").niceScroll({
		cursorcolor: "#bababa", 
		railpadding: { top: 0,  right: 0,  left: 0,  bottom: 0 },
		touchbehavior: false
	}); 
});
$(function() {
	var nice = $(".news-wrap-overflow--1").niceScroll({
		cursorcolor: "#bababa",
		touchbehavior: false
	}); 
});

$(function() {
	$(".talble-prise-mobile__slide__title").click(function () {
		$(this).siblings(".slide-wrap").slideToggle();
		$(this).toggleClass("active-slide")
	})
});
$('.btn-modal--order').click(function () { 
	$.fancybox([ 
		{ href : '#order-form' , 
		padding : 0, 
		wrapCSS    : 'wrap-order-modal'

	}
	]); 
});

$('.btn-modal--call-master').click(function () { 
	if ($(window).outerWidth() > 768) { 
		$.fancybox([ 
		{ 
			href : '#master-modal',  
			closeBtn: false , 
			padding: 0, 
			wrapCSS    : 'wrap-master-modal'
		} 
		]); 

	}else{ 
		$.fancybox([ 
		{ 
			href : '#master-modal',  
			closeBtn: true , 
			padding: 0, 
			wrapCSS    : 'wrap-master-modal'
		} 
		]); 
	} 
	var nice = $(".modal--master").niceScroll({
		cursorcolor: "#bababa"
	}); 
});