function service(){
	var	$block = $('.service-info'),
			$img = $block.find('img'),
			$parentTitle = $block.find('.service-info__text'),
		 	$title = $block.find('h2').detach();

	if ($(window).outerWidth() <= 512) {
		$block.prepend($title);
	}else{
		$parentTitle.prepend($title);
	}
}
service();

$(window).resize(function(){
	service();
})